import React, {useState} from 'react';
import './Footer.css';
import axios from 'axios';
import {Link} from 'react-router-dom';
import {FiFacebook, FiYoutube, FiTwitter, FiInstagram} from 'react-icons/fi';
import swal from 'sweetalert';

function Footer() {
    const [email, setemail] = useState('');

    const validemail =/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i;

    const scrollTop = () =>{
        window.scrollTo({top: 0, behavior: 'smooth'});
      };
    
    const resetEmail=()=>{
        setemail("");
    }

    const handleSubmit = async(e)=>{
    e.preventDefault();
    if(email === ''){
        swal("Oops", "Email is Required", "warning");
    }
    if(!validemail.test(email)){
        swal("Oops", "Email Structure is Invalid", "warning");
    }
    else{
        try{
        await axios.post('/api/newsletter/sendemail', email);
        swal("Good job!", "Thank You For Your Support", "success");
        }catch(error){
        swal("Ooops!", "Something Went Wrong", "error");
        }
    }
    resetEmail();
    }

    return (
        <div className='main-footer-container'>
            <div className='top-footer-container' onClick={scrollTop}>
                <span>Back To Top</span> 
            </div>
            <div className='bottom-footer-container'>
                <div className='bottom-footer-column'>
                 <ul>
                     <li><span className='footer-column-title'>Contact Us</span></li>
                     <li><span className='footer-details'>sachinnimshan@gmail.com</span></li>
                     <li><span className='footer-details'>Follow us on Social Media</span></li>
                     <li className='footer-social-media-icons'><FiFacebook/>{" "}<FiTwitter/>{" "}<FiYoutube/>{" "}<FiInstagram/></li>
                 </ul>
                </div>

                <div className='bottom-footer-column'>
                 <ul>
                     <li><span className='footer-column-title'>Information</span></li>
                     <li><Link className='footer-links'>About Us</Link></li>
                     <li><Link className='footer-links'>Terms and Conditions</Link></li>
                     <li><Link className='footer-links'>Privacy Policy</Link></li>
                     <li><Link className='footer-links'>FAQ</Link></li>               
                 </ul>
                </div>

                <div className='bottom-footer-column'>
                 <ul>
                     <li><span className='footer-column-title'>My Account</span></li>
                     <li><Link className='footer-links'>Sign Up</Link></li>
                     <li><Link className='footer-links'>My Account</Link></li>
                     <li><Link className='footer-links'>My Vouchers</Link></li>
                     <li><Link className='footer-links'>My Wishlist</Link></li>
                 </ul>
                </div>

                <div className='bottom-footer-column'>
                 <ul>
                 <form onSubmit={handleSubmit}>
                     <li><span className='footer-column-title'>Newsletter</span></li>
                     <li><p className='footer-details'>Signup To Our Newsletter</p></li>
                     <li><input placeholder='Your Email' value={email} name='Email' onChange={(e)=> setemail(e.target.value)} className='newsletter-field'/>
                     <li><button type='submit' className='newsletter-btn'>Subscribe</button></li></li>
                     </form>
                 </ul>
                </div>               
            </div>

            <div className='footer-end-container'>
                    <div className='website-copyright-container'>
                    <span className='copyright-container'>
                     <a href=''>SNMART</a> (2021)  Solution by <a href=''>Sachin Nimshan</a>
                    </span>
                    </div>
                    <div className='payment-methods-container'>
                    <img src='/Images/payments.png'/>
                    </div>
            </div>
            
        </div>
    )
}

export default Footer;
