import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import {Link} from 'react-router-dom';
import {Card, ListGroup, Badge} from 'react-bootstrap';
import './Products.css';
import formatCurrency from '../Currency';

export default function Products (props){
    const { product } = props;

        return (
        <div className='main-products-container'>
            <div className="card" 
            style={{width: "15rem", borderRadius: "0", border: "none", borderTop: "3px solid lightgray"}} 
            key={product._id}>

            <Link to={`/product/${product._id}`}><img style={{height: "16rem", padding: "1rem 1rem"}} src={product.Image} className="card-img-top"/></Link>
            <div className="card-body">
            <h5 className="card-title"><span className='single-product-title'>
            <Link to={`/product/${product._id}`}>{product.Title}</Link></span></h5>
            <h1><span className='single-product-price'>{formatCurrency(product.Price)}</span></h1>
            <h5>{(product.CountInStock > 0) ? (<span class="badge badge-success">In Stock</span>) : (<span class="badge badge-danger">Out Of Stock</span>)}</h5>
            </div>
          </div>
                
        </div>
        )
    
}