import React, {useEffect} from 'react';
import CheckOutScreen from '../CheckOut/CheckOutScreen';
import './PlaceOrder.css';
import {Button} from 'react-bootstrap';
import {useDispatch, useSelector} from 'react-redux';
import formatCurrency from '../Currency';
import {Link} from 'react-router-dom';
import { CREATE_ORDER_RESET } from '../Constants/PlaceOrderConstants';
import { CreateOrder } from '../Actions/PlaceOrderActions';
import LoadingBox from '../Components/LoadingBox';
import MessageBox from '../Components/MessageBox';

function PlaceOrder(props) {

    const Cart = useSelector(state=> state.Cart);
    const {ShippingAddress, PaymentMethod, CartItems} = Cart;

    if(!PaymentMethod){
        props.history.push('/payments');
    }

    const dispatch = useDispatch();
    const OrderCreate = useSelector(state=> state.OrderCreate);
    const {loading, success, error, order} = OrderCreate;

    const ToPrice = (num)=> Number(num.toFixed(2)); //'5.123'=> '5.12'=> 5.12
    Cart.ItemsPrice = ToPrice(Cart.CartItems.reduce((a,c)=> a + c.qty* c.Price, 0));

    Cart.ShippingPrice = ((Cart.ItemsPrice > 100)?(ToPrice(0)):(ToPrice(10)));
    Cart.TaxPrice = ToPrice(0.15*(Cart.ItemsPrice));
    Cart.TotalPrice = (Cart.ItemsPrice + Cart.TaxPrice + Cart.ShippingPrice);

    const PlaceOrderSubmitHandler=()=>{
        dispatch(CreateOrder({...Cart, OrderItems: Cart.CartItems}));
    }
    
    useEffect(()=>{
        if(success){
            props.history.push(`/order/${order._id}`);
            dispatch({type: CREATE_ORDER_RESET});
        }
    }, [dispatch, order, props.history, success]);

    return (
        <div className='main-placeorder-container'>
            <CheckOutScreen step1 step2 step3 step4></CheckOutScreen>
            <div className='container-fluid d-flex flex-wrap justify-content-center mt-3'>

            <div><div className="row">
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Shipping</h5>
                <div className="card-body">
                 <p className="card-text"><strong>Name : </strong>{ShippingAddress.FullName}</p>
                 <p className="card-text"><strong>Address : </strong>{ShippingAddress.Address}</p>
                 <p className="card-text"><strong>City : </strong>{ShippingAddress.City}</p>
                 <p className="card-text"><strong>Postal Code : </strong>{ShippingAddress.PostalCode}</p>
                 <p className="card-text"><strong>Country : </strong>{ShippingAddress.Country}</p>
                 </div>
                </div>
               </div>
             </div>

             <div className="row">
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Payments</h5>
                <div className="card-body">
                 <strong>Method : </strong>{PaymentMethod}
                 </div>
                </div>
               </div>
             </div></div>

             <div className="row">
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Ordered Items</h5>
                <div className="card-body">
                {CartItems.map((item)=>(
                        <div className='container mb-3'>
                        <div><img className='place-order-image' key={item._id} src={item.Image}/></div>
                        <div><Link to={`/product/${item.product}`}>
                            <strong>{item.Title}</strong></Link></div>
                        <div><strong>
                        {item.qty} X {formatCurrency(item.Price)} = {formatCurrency((item.qty)*(item.Price))}
                        </strong>
                        </div>
                        </div>
                    ))}
                 </div>
                </div>
               </div>
             </div>

             <div className="row">
             <div>
                    {loading && <LoadingBox></LoadingBox>}
                    {error && <MessageBox>{error}</MessageBox>}
                </div>
             <div className="col">
              <div className="card border-0 order">
              <h5 className="card-header">Order Summary</h5>
                <div className="card-body summary">
                 <ul>
                        <li>Items : {formatCurrency(Cart.ItemsPrice)}</li>
                        <li>Shipping : {formatCurrency(Cart.ShippingPrice)}</li>
                        <li>Taxes : {formatCurrency(Cart.TaxPrice)}</li>
                        <li><strong>Order Total : {formatCurrency(Cart.TotalPrice)}</strong></li>
                    </ul>
                   <div> <Button className='place-order-btn'
                   onClick={PlaceOrderSubmitHandler}
                   disabled={Cart.CartItems.length === 0}>Place Order</Button></div>
                 </div>
                </div>
               </div>
             </div>


            </div>
        </div>
    )
}

export default PlaceOrder;
